/*global chrome, $*/

import React, { Component } from 'react';

class Profile extends Component {

    constructor(props) {
        super(props);

        this.service  = chrome.extension.getBackgroundPage().getService2();
        this.auth  = chrome.extension.getBackgroundPage().getAuth();
        this.changePasswordSubmit = this.changePasswordSubmit.bind(this);
        this.changeHandler = this.changeHandler.bind(this);
        this.deleteAccount = this.deleteAccount.bind(this);
        this.state = {
            name: '',
            email: '',
            twitter: '',
            instagram: '',
            linkedin : '',
            personal_website: '',
            organization_name: '',
            organization_website: '',
            position: '',
            team_size: '',
            newsletter: false,
            current_password: '',
            new_password: '',
            confirm_password: '',
            errors: {},
            cpErrors: {},
            profileChangedSuccess: false,
            changePasswordSuccess: false,
            profileProcess: false,
            changePasswordProcess: false
        }
        var user = JSON.parse(localStorage.getItem('qleary_user'));
        if(user){
            this.state.name = user.name;            
            this.state.email = user.email;
            this.state.twitter = user.twitter || '';
            this.state.linkedin = user.linkedin || '';
            this.state.instagram = user.instagram || '';
            this.state.personal_website = user.personal_website || '';
            this.state.organization_name = user.organization_name || '';
            this.state.organization_website = user.organization_website || '';
            this.state.position = user.position || '';
            this.state.team_size = user.team_size || '';
            this.state.newsletter = (user.newsletter.toString() === "1");            
        }
    }
   
    profileSubmit(e){
        e.preventDefault();     

        var _ = this;
        if(this.handleValidation()) {

            this.setState({ profileProcess: true });

            let para = new FormData();

            para.append('name', this.state.name);
            para.append('email', this.state.email);
            para.append('twitter', this.state.twitter);
            para.append('linkedin', this.state.linkedin);
            para.append('instagram', this.state.instagram);
            para.append('personal_website', this.state.personal_website);
            para.append('organization_name', this.state.organization_name);
            para.append('organization_website', this.state.organization_website);
            para.append('position', this.state.position);
            para.append('team_size', this.state.team_size);
            para.append('newsletter', (this.refs.newsletter.checked)? 1 : 0);

            this.service.post(this.service.version + 'profile', para).then(function(response) {
                
                if(response.success) {
                    var user = _.auth.user();

                    user.name = response.result.name;
                    user.email = response.result.email;                    
                    user.twitter = response.result.twitter;                    
                    user.linkedin = response.result.linkedin;                    
                    user.instagram = response.result.instagram;                    
                    user.personal_website = response.result.personal_website;                    
                    user.organization_name = response.result.organization_name;                    
                    user.organization_website = response.result.organization_website;                    
                    user.position = response.result.position;                    
                    user.team_size = response.result.team_size;                    
                    user.newsletter = response.result.newsletter;

                    _.auth.setUser(user);
                    _.setState({ profileChangedSuccess : true, profileProcess: false });
                    setTimeout(function() { 
                        _.setState({ profileChangedSuccess : false }); 
                    }, 5000);
                } 
                
            })
            .catch(function(response){
                response = JSON.parse(response);
                if(response.hasOwnProperty('email')) {
                    _.setState({ errors : { email: response.email[0] } });
                }
            });

        }
    }

    changePasswordSubmit(e) {
        e.preventDefault();

        var flag = true;
        var cpErrors = {};
 

        if(!this.state.current_password) {
            cpErrors['current_password']  = "This field is required.";
            flag = false;
        }
 
        if(!this.state.password) {
            cpErrors['password']  = "This field is required.";
            flag = false;
        }

        if(!this.state.confirm_password) {
            cpErrors['confirm_password']  = "This field is required.";
            flag = false;
        } else if(this.state.confirm_password !== this.state.password) {
            cpErrors['confirm_password']  = "The password and confirm password should be the same.";
            flag = false;
        }


        this.setState({ cpErrors : cpErrors });

        if(flag) {

            this.setState({ changePasswordProcess: true });
            var _ = this;

            let para = new FormData();

            para.append('current_password', this.current_password);
            para.append('new_password', this.password);
            para.append('confirm_password', this.confirm_password);

            this.service.post('admin/post-change-password', para).then(function(response) {
                
                if(response.success) {

                    _.setState({
                        current_password: '',
                        password: '',
                        confirm_password: '',
                        changePasswordSuccess : true
                    });

                    setTimeout(function() { 
                        _.setState({ changePasswordSuccess : false }); 
                    }, 5000);
                } else {
                    alert(response.message);
                }
                _.setState({ changePasswordProcess : false }); 
            })
            .catch(function(response){
                _.setState({ changePasswordProcess : true }); 
            });
        }
    }

    deleteAccount() {
        
        var that = this;
        if(window.confirm('Are you sure you want to delete your account? This will delete all the data from the server. You will never be able to recover your data.')) {
                

            $(this.refs.delete_account).html('Removing Please wait...');
            
            this.service.post(this.service.version + 'profile/remove', new FormData()).then(function(response) {
                  
                that.auth.logout();
                document.location.reload();
                
            })
            .catch(function(response){ console.log(response) });
        }
    }
    

    handleValidation() {

        var flag = true;
        var errors = {};

        if(!this.state.name.trim()) {
            errors['name']  = "This field is required.";
            flag = false;
        }

        if(this.state.twitter.trim()) {
            if(!this.isValidUsername(this.state.twitter)) {
                if(!this.isUrlValid(this.state.twitter.trim())) {
                    errors['twitter']  = "Please enter a valid url or username.";
                    flag = false;
                }
            }
        }

        if(this.state.linkedin.trim()) {
            if(!this.isValidUsername(this.state.linkedin)) {
                if(!this.isUrlValid(this.state.linkedin.trim())) {
                    errors['linkedin']  = "Please enter a valid url or username.";
                    flag = false;
                }
            }
        }

        if(this.state.instagram.trim()) {
            if(!this.isValidUsername(this.state.instagram)) {
                if(!this.isUrlValid(this.state.instagram.trim())) {
                    errors['instagram']  = "Please enter a valid url or username.";
                    flag = false;
                }
            }
        }
        if(this.state.personal_website.trim()) {
            if(!this.isUrlValid(this.state.personal_website.trim())) {
                errors['personal_website']  = "Please enter a valid url.";
                flag = false;
            }
        }
        if(this.state.organization_name.trim()) {
            if(!this.isUrlValid(this.state.organization_name.trim())) {
                errors['organization_name']  = "Please enter a valid url.";
                flag = false;
            }
        }
        if(this.state.organization_website.trim()) {
            if(!this.isUrlValid(this.state.organization_website.trim())) {
                errors['organization_website']  = "Please enter a valid url.";
                flag = false;
            }
        }
        
        if(this.state.team_size) {
            if(isNaN(Number(this.state.team_size))) {
                errors['team_size']  = "Team size must be a number..";
                flag = false;
            }
        }

        if(!this.state.email) {
            errors['email']  = "This field is required.";
            flag = false;
        } else  if(!this.isEmailValid(this.state.email)) {
            errors['email']  = "Please enter a valid email address.";
            flag = false;
        }

        this.setState({
			errors : errors
		});

        return flag;
    }
    
    isEmailValid(email) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }
    isValidUsername(username) {
        return /^[A-Za-z0-9_@./#&+-]*$/.test(username.trim());
    }
    isUrlValid(url) {
        var pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
        '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
        '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
        '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
        '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
        '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
        return !!pattern.test(url);
    
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(url));
    }

    changeHandler(event) {
        const name = event.target.name;

        this.setState({
            [name]: event.target.value
        });
    }
    render() {
        return (  
        <div className="white_box animated fadeInUp">              
            <form method="post" onSubmit= {this.profileSubmit.bind(this)}>
                <div class="row">
                    <div class="col-sm-12 pb-4">
                  </div>
                    <div class="col-sm-6">
                        <label class="text">Name</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Name" type="text" value={this.state.name} ref="name" onChange={this.changeHandler} name="name"  />
                            <span class="text-danger"><small>{ this.state.errors['name'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                    <label class="text">Email</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Email" type="text" ref="email" value={this.state.email} name="email" onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.errors['email'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <label class="text">Twitter</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Twitter" type="text" value={this.state.twitter} name="twitter" onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.errors['twitter'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <label class="text">Instagram</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Instagram" type="text" value={this.state.instagram} name="instagram" onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.errors['Instagram'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <label class="text">LinkedIn</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="LinkedIn" type="text" value={this.state.linkedin} name="linkedin" onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.errors['linkedin'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <label class="text">Personal website</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Personal website" type="text" ref="personal_website" value={this.state.personal_website} name="personal_website" onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.errors['personal_website'] }</small></span>
                        </div>
                    </div> 
                    <div class="col-sm-6">
                        <label class="text">Organization website</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Organization website" type="text" ref="organization_website" value={this.state.organization_website} name="organization_website" onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.errors['organization_website'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <label class="text">Position</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Position" type="text" ref="position" value={this.state.position} name="position" onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.errors['position'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <label class="text">Team size</label>
                        <div class="form-group">
                            <input class="form-control" placeholder="Team size" type="text" ref="team_size" value={this.state.team_size} name="team_size" onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.errors['team_size'] }</small></span>
                        </div>
                    </div>

                    <div class="col-sm-12 pb-4">
                        <label class="checkbox-inline text">
                            <input type="checkbox" value="1" className="width-auto" ref="newsletter" defaultChecked={this.state.newsletter} onChange={this.changeHandler} /> 
                            &nbsp; Receive updates and more. We will never spam you, don't worry! { this.state.newsletter }
                        </label>
                    </div>
                    { (this.state.profileChangedSuccess)? <div class="col-sm-12 mt-4" >
                        <div class="alert alert-success">
                            <strong>Success!</strong> Profile has been updated.
                        </div>
                    </div> : null }

                    <div class="col-sm-12 mt-4 text-center">
                        <button class="btn btn-primary kbd p-2 px-4 m-auto">
                            { (this.state.profileProcess)? 'Processing...' : 'Save' }
                        </button>
                    </div> 
                </div>
            </form>
            <form method="post" onSubmit= {this.changePasswordSubmit} >
                <div class="row">
                    <div class="col-sm-12 pb-4 mt-5">
                        <h4 class="headingSec">Change Password</h4>
                    </div>
                    <div class="col-sm-12">
                        <div class="form-group">                        
                            <input class="form-control" placeholder="Current Password" type="password" ref="current_password" name="current_password" value={this.state.current_password} onChange={this.changeHandler} />
                            <span class="text-danger"><small>{ this.state.cpErrors['current_password'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <div class="form-group">
                            <input class="form-control" placeholder="Password" type="password" ref="password" name="password" value={this.state.password} onChange={this.changeHandler}  />
                            <span class="text-danger"><small>{ this.state.cpErrors['password'] }</small></span>
                        </div>
                    </div>
                    <div class="col-sm-6">
                        <div class="form-group">
                            <input class="form-control" placeholder="Confirm Password" type="password" ref="confirm_password" name="confirm_password" value={this.state.confirm_password} onChange={this.changeHandler}  />
                            <span class="text-danger"><small>{ this.state.cpErrors['confirm_password'] }</small></span>
                        </div>
                    </div>

                    { (this.state.changePasswordSuccess)? <div class="col-sm-12 mt-4">
                        <div class="alert alert-success">
                            <strong>Success!</strong> Password has been changed successful.
                        </div>
                    </div> : null }
                    
                    <div class="col-sm-12 mt-4 text-center">
                        <button class="btn btn-primary kbd p-2 px-4 m-auto">                            
                            { (this.state.changePasswordProcess)? 'Processing...' : 'Change' }
                        </button>
                    </div>
                </div> 
            </form>

            <div class="col-sm-12 mt-4 text-center">
                <button class="btn btn-danger kbd p-2 px-4 m-auto" ref onClick={this.deleteAccount} ref="delete_account" >Delete account</button>
            </div> 

        </div>   
    );
  }
}

export default Profile;
