/* global chrome, $, WEB_URL, board, entry, sync2 */

import React, { Component } from 'react';

class Backup extends Component {
  constructor(props) {
    super(props);
    this.service = chrome.extension.getBackgroundPage().getService();
    this.page = 1;
    this.state = {
        backups: [],
        moreRecords: false,
        restoring: false,
        manualBackup: false,
        uploadBackup: false
    }
  }

  componentDidMount () {
      this.getData();
  }

  getData() {
	$('.big-centered-loader').show();
    this.service.get(this.service.version + 'backup?page=' + this.page)
    .then( ( respponse ) => {
		$('.big-centered-loader').hide();
        console.log("respponse", respponse)
        this.page++;
        this.setState({ 
            backups: this.state.backups.concat(respponse.data),
            moreRecords: this.page < respponse.last_page
         });
    });
  }
  
  restore(unique_id) {
    if(this.state.restoring) {
        return;
    }

    window.confirmBox.callback = (response) => {
        $('#confirm_box').modal('hide');

        var fd = new FormData();
    
        fd.append('file', window.backupfile);
        fd.append('type', (response == 'cancel')? 'merging': 'restore');


        this.setState({restoring: unique_id});
        this.service.post(this.service.version + `backup/${unique_id}/restore`, fd)
        .then( ( response ) => {
            console.log("respponse", response) 

            if(response.success) {
                board.saveRemoved([]);
                entry.saveRemoved([]);
                sync2(() => {
                    this.setState({restoring: false});
                }, true, {
                    url: this.service.version + "auth/sync"
                });
                
                alert(response.message);
                return;
            }
            alert(response.message);

        });
        
    }

    window.confirmBox.cancel = window.confirmBox.callback;
    
    window.confirmBox.init(
        'Please choose an option to restore',
        'asset/image/backup2.png',
        {
            'button_1_text' : 'Restore to Backup',
            'button_2_text' : 'Merging',

        }
    );


    
  }
  
  createBackup() {
    if(this.state.manualBackup) {
        return;
    }
    this.setState({manualBackup: true});
    this.service.post(this.service.version + `backup`, new FormData())
    .then( ( response ) => {
        if(response.success) {
            this.setState({backups: []});
            this.page = 1;
            this.getData();
            
            alert(response.message);
            this.setState({manualBackup: false});

            return;
        }
        alert(response.message);

    });
  }
  
  uploadBackup(event) {
    if(this.state.uploadBackup) {
        return;
    }
    
    window.backupfile = event.target.files[0];
    window.confirmBox.callback = (response) => {
        var fd = new FormData();
        fd.append('file', window.backupfile);
        fd.append('type', (response === 'cancel')? 'merging': 'restore');

        $('.big-centered-loader').show();
        this.service.post(this.service.version + `backup-restore`, fd)
        .then( ( response ) => {
            
            if(response.success) {
                sync2(() => {
	                $('.big-centered-loader').hide();
                    alert(response.message);
                }, true, {
                    url: this.service.version + "auth/sync"
                });
                return;
            }
	        $('.big-centered-loader').hide();
            alert(response.message);
        });
    }

    window.confirmBox.cancel = window.confirmBox.callback;
    
    window.confirmBox.init(
        'Please choose an option to restore',
        'asset/image/backup2.png',
        {
            'button_1_text' : 'Restore to Backup',
            'button_2_text' : 'Merging',

        }
    );


  }
  
  
  render() {
    return (  
        <div className="animated fadeInUp">
            <div class="row board-row " >
                <div class="col-sm-12 media align-items-center my-3">
                    <div class="media-left pr-2">
                        <img src="asset/image/backup2.png" height="21" />
                    </div>
                    <div class="media-body">
                        <button class="btn btn-primary btn-sm mb-2 ml-2 float-right"
                            disabled={this.state.manualBackup}
                            onClick={this.createBackup.bind(this)} >
                            {(this.state.manualBackup)? 'Wait...' : 'Create Backup'}
                        </button>
                        
                        <button class="btn btn-danger btn-sm mb-2 ml-2 float-right custom-select-file"
                            disabled={this.state.uploadBackup} >
                            <input type="file" name="upload_backup" 
                                onChange={this.uploadBackup.bind(this)} />
                            {(this.state.uploadBackup)? 'Wait...' : 'Upload Backup'}
                        </button>
                        <h4 class="media-body m-0"> 
                            Backup Management
                        </h4>
                        
                    </div>
                     
                </div>
                <div class="col-md-12 mb-5">
                    <div class="card px-3 backup-m-card">

                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Size</th>
                                    <th>Active</th>
                                    <th>Backup At</th>
                                    <th className="text-center">Action</th>
                                </tr>
                            </thead>
                            <tbody>
                               
                                { this.state.backups.map((backup, index) => ( 
                                    <tr>
                                        <td>{ ++index }</td>
                                        <td>{ backup.size }</td>
                                        <td>{ backup.available }</td>
                                        <td>{ backup.created_at }</td>
                                        <td className="text-center">
                                            { (backup.available === 'Yes')? (
                                                <>
                                                <a target="_blank"
                                                    class="btn btn-primary btn-sm mb-2"
                                                    href={WEB_URL +'/x/download-backup/' + backup.unique_id }>
                                                        Download
                                                </a>
                                                <button class="btn btn-danger btn-sm mb-2 ml-2"
                                                    disabled={this.state.restoring == backup.unique_id}
                                                    onClick={() => this.restore(backup.unique_id)} >
                                                    {(this.state.restoring === backup.unique_id)? 'Restoring...' : 'Restore'}
                                                </button>
                                                </>
                                            ) : null }
                                            
                                        </td>
                                    </tr>
                                ))}

                                { (this.state.backups.length < 1)? (
                                    <tr>
                                        <td colSpan="5" className="text-center"> 
                                            There are no backups yet. Click the button below to create your first backup.
                                        </td> 
                                    </tr>
                                ) : null }

                            </tbody>
                        </table>
                        
                    </div>
                    { (this.state.moreRecords)? (
                        <div className="row ">
                            <div className="col-md-12 text-center py-5">
                                <button class="btn btn-primary btn-sm" onClick={() => this.getData()}>Load More</button>        
                            </div>
                        </div>
                    ) : null}
                    
                </div>
            </div>

    </div>   
    );
  }
}

export default Backup;

